import { Directive, Input, ElementRef, Renderer2, OnInit, ChangeDetectorRef, AfterContentInit } from '@angular/core';
import { DomAttributesService } from '../services/dom-attributes.service';

@Directive({
  selector: '[bbDynamicId]',
  exportAs: 'bbDynamicId',
})
export class DynamicIdDirective implements AfterContentInit {
  constructor(
    private readonly _elRef: ElementRef,
    private readonly _renderer: Renderer2,
    private readonly domAttributesService: DomAttributesService,
    private readonly cd: ChangeDetectorRef,
  ) {}

  @Input() bbDynamicId: string | undefined;

  ngAfterContentInit() {
    setTimeout(() => {
      this._renderer.setAttribute(this._elRef.nativeElement, 'id', this.domAttributesService.nextId(this.bbDynamicId));
      this.cd.markForCheck();
    });
  }
}
